    .header{
        
        align-content: left;
        width: 400px;
        
        position: relative;
        margin: 10px;
        z-index: 1;
        
        
       
    }
    .title{
        
        /* font-size: 40px;
        for ui */
        font-size: 40px;
        line-height: 50px;
        color: rgb(255, 255, 255);
        font-family: var(--headingfont);
        text-overflow:inherit;
            margin: 0px;
    }
    .bar{
        
        height: 6px;
        width: 320px;
        
        
        background-color: rgb(255, 255, 255);
        
    }
    .text{
        
        margin-top: 10px;
        font-size: 0.75em;
        color: rgb(255, 255, 255); 
        text-align: left;
       font-family: var(--headingfont);
       margin-bottom: 35px;
    }

    .sub-menu a{
        margin-left: 15px;
        text-decoration: none;
        line-height: 0px;
        
        font-size: 20px;
        color: rgb(255, 255, 255);
        font-family: var(--headingfont);
    }
    .sub-menu a:hover{
        font-size: 23px;
    }

.sub-header{
    color: rgba(255, 255, 255, 0.5);       
    float: right;
    z-index: 1;  
    right:0px;
    bottom:10px;
    font-style: italic; 
    position: fixed;
    opacity: 1;
    margin-right: 40px;
    font-size: 0.75em;
    
    }



.project_annotations{
    
    float: right;
    z-index: 1;  
    right:40px;
    top:28px;
    position: fixed; 
    margin-right: 5%;
    font-size: 2em;
    background-color: rgba(0, 0, 0, 0);
    padding: 0;
    border: none;
    font-family: monospace;
    margin: 0px;
    /* border: tomato 1px solid; */
    color: white;
}

.project_annotations a{
    
    
    color: white;
}
.project_annotations_text{
    font-size: 12px;
    text-align: right;
    /* border: tomato 1px solid; */   
    color: white;
}
.project_mouse_annotations{
    font-size: 12px;
    text-align: right;
}
#cursorX{
    z-index: 1;  
    background-color: rgba(0, 0, 0, 0);
    padding: 0;
    border: none;
    width: 25px;
    color: rgb(255, 255, 255);   
    font-size: 0.75em;
    /* border: tomato 1px solid; */
    }
    #cursorY{
    z-index: 1;  
    background-color: rgba(0, 0, 0, 0);
    padding: 0;
    border: none;
    width: 25px;
    color: rgb(255, 255, 255);
    font-size: 0.75em;
    /* border: tomato 1px solid; */     
    }
    
@media all and  (max-width: 1300px) {
    .project_annotations{
        opacity: 0;
    }
    .project_back_link a{    
        opacity: 0;    
    }
        
}